@use 'variables' as v;

footer {
  background-color: v.$primaryDarkModerateCyan;
  height: 200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .footerNav {
    margin: 0 auto;
    padding: 20px 0;
    width: 300px;
    text-align: center;

    nav {
      ul {
        margin-top: 24px;
        text-align: center;
        display: flex;
        justify-content: center;

        li {
          display: inline;
          transition: 0.2s;
          padding: 0 20px;

          a {
            color: v.$primaryDarkDesaturated;
          }

          &:hover {
            a {
              color: v.$primaryWhite;
            }
          }
        }
      }
    }

  }

  .socialBar {
    margin-top: 16px;
    text-align: center;

    a {
      padding: 0 8px;
    }
  }
}
